                  <li>
                  <ul class="cjkul">
                    <li></li>
                    <li></li>
                  </ul>
                  </li>
                  <li></li>
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
                </li>
                <li></br>
                <ul class="cjkul">
                  <li></li>
                  <li></li>
                </ul>
                </li>
                <li>
                <ul>
                  <li></li>
                  <li></li>
                  <li>IDID</li>
                  <li>
                  <ul class="thirdul">
                    <li></li>
                    <li></li>
                    <li></li>
                  </ul>
                  </li>
                  <li>18.41</li>
                  <li></li>
                  <li>
                  <ul class="thirdul">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li>4.7.3</li>
                  </ul>
                  </li>
                  <li>
                  <ul class="thirdul">
                    <li></li>
                    <li> </li>
                    <li></li>
                    <li>4.8.1.</li>
                    <li></li>
                  </ul>
                  </li>
                </ul>
                </li>
              </ul>`;
          break;
        case "privacy":
          this.typename = "";
          innerhtml = `<p>
          </p>
          <ul class="ulgroup">
            <li>
                <ul>
                <li></li>
                <li></li>
                <li>JOB</li>
