};
</script>

<style scoped>
.pointer {
  cursor: pointer;
}
.w-100 {
  width: 100%;
}
.w-70 {
  width: 70%;
}
.h-100 {
  height: 100%;
}
.flex-r {
  flex-direction: row;
}
.flex-c {
  flex-direction: column;
}
.position-r {
  position: relative;
}

#service {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: 7;
}

.serviceFrame,
.privacyFrame {
  background-image: url("../assets/faq_frame-01.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.scrollbar {
  overflow-x: hidden;
  overflow-y: auto;
  max-width: 90%;
  max-height: 40%;
  margin-bottom: 5%;
  max-height: 350px;
}
::-webkit-scrollbar {
  width: 15px;
}
::-webkit-scrollbar-thumb {
  background: rgb(186, 222, 250);
  border-radius: 10px;
}
::-webkit-scrollbar-track {
  background-color: rgb(50, 50, 50);
  border: 2px solid rgb(255, 255, 255);
  border-radius: 10px;
}
.flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex: 1;
}

.serviceText {
  color: rgb(255, 255, 255);
  font-size: 2vmin;
  letter-spacing: 2px;
  text-align: left;
}
.title {
  color: rgb(255, 255, 255);
  font-size: 4vmin;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: top;
  flex: 1;
}
.back_btn {
  position: absolute;
  height: 10vh;
  width: 5vw;
  top: 0vh;
  left: -2vw;
  background-image: url("../assets/back_btn-01.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 5;
}
.close_btn {
  height: 10vh;
  width: 5vw;
  position: absolute;
  top: 0vh;
  right: -4%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../assets/close_btn-01.svg");
  z-index: 5;
  flex: 1;
}

#content >>> ul {
  list-style: none;
}
#content >>> .ulgroup {
  counter-reset: chapter;
}
#content >>> .ulgroup > li {
  counter-increment: chapter;
  counter-reset: section;
  margin-left: 0.5em;
}
#content >>> .ulgroup > li::before {
  content: counter(chapter) ".";
  margin-right: 0.5em;
}
#content >>> .ulgroup > li > ul:not(.cjkul) > li {
  counter-increment: section;
  margin-left: 0.5em;
}
#content >>> .ulgroup > li > ul:not(.cjkul) > li::before {
  content: counter(chapter) "." counter(section) ".";
  margin-right: 0.5em;
}

#content >>> .cjkul > li {
  counter-increment: cjk;
  margin-left: 0.5em;
}
#content >>> .cjkul > li::before {
  content: counter(cjk, cjk-ideographic) "";
  margin-right: 0.5em;
}
#content >>> .ulgroup > li > ul > li > .thirdul > li {
  counter-increment: third;
  margin-left: 0.5em;
}
#content >>> .ulgroup > li > ul > li > .thirdul > li::before {
  content: counter(chapter) "." counter(section) "." counter(third) ".";
  margin-right: 0.5em;
}

