      outbox_w = (gamegroup_w - bg_w) / 2;
      console.log(gamegroup_h, gamegroup_w, gamegroup_outbox_h, scrollbox_h);
      if (gamegroup_outbox_h > 0) {
        this.game_outbox_h = gamegroup_outbox_h;
      } else {
        this.game_outbox_h = 0;
      }
      if (outbox_w < 0) {
        this.faq_outbox_w = 0;
      } else {
        this.faq_outbox_w = outbox_w;
      }
      this.scrollbox_h = scrollbox_h;
    },
